import {WOW} from 'wowjs'
export default {
    props: {
        obj: {
            type: Object
        }
    },
    watch: {
        'obj.animateImg': {  
    handler(newValue, oldValue) { 
                this.$nextTick(() => { // dom,
                    var wow = new WOW({
                        live: false
                    })
                    wow.init()
                })
    },  
    deep: true  
    },
        'obj.animateText': {  
    handler(newValue, oldValue) {  
                this.$nextTick(() => { // dom,
                    var wow = new WOW({
                        live: false
                    })
                    wow.init()
                })
    },  
    deep: true  
    },
        'obj.animateTitle': {  
    handler(newValue, oldValue) {  
                this.$nextTick(() => { // dom,
                    var wow = new WOW({
                        live: false
                    })
                    wow.init()
                })
    },  
    deep: true  
    },
    },
    mounted() {
        this.$nextTick(() => { // dom,
            this.hackRest = true
          var wow = new WOW({
            live: false
          })
          wow.init()
        })

    },
    methods: {
        goClick(url) {
            if(url) {
                window.open(url)
            }
            
        }
    }
}