<template>
    <div class="setComponent">
        <div class="form-item">
            <el-tooltip class="item" effect="dark" content="5" placement="top-start">
                <label><i style="color:#ccc;" class="el-icon-info"></i></label>
            </el-tooltip>         
            <el-slider v-model="obj.classOptions.limitMoveNum" :min="1" :max="10"></el-slider>
        </div>  
        <div class="form-item">
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
                <label> <i style="color:#ccc;" class="el-icon-info"></i></label>
            </el-tooltip>    
            <el-input-number v-model="obj.classOptions.step"></el-input-number>          
        </div>   
        
        <div class="form-item">
            <label> <i style="color:#ccc;" class="el-icon-info"></i></label>  
            <el-input-number v-model="obj.padding"></el-input-number>   
        </div>
        <div class="form-item">
            <label> <i style="color:#ccc;" class="el-icon-info"></i></label>
            <el-input-number v-model="obj.width"></el-input-number>
        </div>
        <div class="form-item">
            <label> <i style="color:#ccc;" class="el-icon-info"></i></label>  
            <el-input-number v-model="obj.height"></el-input-number>
        </div>
        <div class="form-item">
            <label></label>
            <el-select v-model="obj.classOptions.direction" clearable placeholder="">
                <el-option
                    v-for="(item,itemIndex) in directionOptions"
                    :key="itemIndex"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div class="form-item">
            <label>hover</label>
            <el-switch
            v-model="obj.classOptions.hoverStop"
            active-color="#13ce66"
            inactive-color="#ff4949">
            </el-switch>
        </div>
        <div class="form-item">
            <label></label>
            <el-button size="small" type="primary" @click="uploadClick"></el-button>
        </div>
    </div>
</template>
<script>
import { animateOptions } from '@/utils/animateOptions'
import { columnOptions } from '@/utils/selectOptions'
export default {
    name: 'ThreeColumns',
    props: {
        obj: {
            type: Object,

        }
    },
    data() {
        return {
            options: animateOptions,
            columnOptions: columnOptions,
            directionOptions: [
                {label: '',value: 0},
                {label: '',value: 1},
                {label: '',value: 2},
                {label: '',value: 3},
            ]
        }
    },
    methods: {
        uploadClick() {
            this.$emit('uploadClick',this.obj)
        },
    }
}
</script>
