<template>
    <div class="setComponent">
        <div class="form-item">
            <label></label>
            <el-button size="mini" @click="butClick(['title', obj.title])"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-button size="mini" @click="butClick(['smTitle', obj.smTitle])"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-button size="small" type="primary" @click="imgloadClick"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-select v-model="obj.animateTitle" clearable placeholder="">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
        <div class="form-item">
            <label></label>
            <el-select v-model="obj.animateText" clearable placeholder="">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
    </div>
</template>
<script>
import { animateOptions } from '@/utils/animateOptions'
export default {
    name: 'CoverImg',
    props: {
        obj: {
            type: Object,

        }
    },
    data() {
        return {
            options: animateOptions
        }
    },
    methods: {
        butClick(msg) {
            this.$emit('butClick',msg)
        },
        imgloadClick() {
            this.$emit('imgloadClick',this.obj)
        }
    }
}
</script>
